@import "../../styles/AppDesign.scss";
.viewport-animtion{
    position:relative;
}

.viewport-animtion__body{
    transition: .4s;
    transform-origin: center top;
    transform: translateY(10%) scaleY(1.2);
    opacity:0;
    .viewport-animtion.is-active &{
        opacity:1;
        transform: translateY(0) scaleY(1);
    }
}